<template>
    <section class="calculator-container">
        <h2>Simple Calculator:</h2>
       <div class="calculator grid">
            <input class="calc-display" type="text" disabled v-model="calculator.display">
            <button @click="handleOp" class="btn">DEL</button>
            <button @click="handleCalc" class="btn">7</button>
            <button @click="handleCalc" class="btn">8</button>
            <button @click="handleCalc" class="btn">9</button>
            <button @click="handleOp" class="btn">+</button>
            <button @click="handleCalc" class="btn">4</button>
            <button @click="handleCalc" class="btn">5</button>
            <button @click="handleCalc" class="btn">6</button>
            <button @click="handleOp" class="btn">-</button>
            <button @click="handleCalc" class="btn">1</button>
            <button @click="handleCalc" class="btn">2</button>
            <button @click="handleCalc" class="btn">3</button>
            <button @click="handleOp" class="btn">/</button>
            <button @click="handleCalc" class="btn">0</button>
            <button @click="handleOp" class="btn">.</button>
            <button @click="handleOp" class="btn">=</button>
            <button @click="handleOp" class="btn">X</button>
       </div>
    </section>
</template>

<script>
export default {
    data() {
        return {
            calculator: {
                operator: null,
                display: 0,
                isNextInput: false,
                score: null,
                keys: []
            }
        }
    },
    methods: {
        handleCalc(el) {
            let val = el.target.innerText

            if (this.isNumber(val)) {
                if (this.calculator.display !== 0 && !this.calculator.isNextInput) { // first input to calculator - concatenating values to display 
                    this.calculator.display += val
                }
                else if (this.calculator.display !== 0 && this.calculator.isNextInput && this.calculator.operator) { // second input to calculator - reseting the display & calculator act like first input
                    this.calculator.display = val
                    this.calculator.isNextInput = false
                }
                else { // first input to calculator - reseting the display to the value
                    this.calculator.display = val
                }

            }
            else {
                return
            }
            
        },
        handleOp(el) {
            let val = el.target.innerText
            let num = Number(this.calculator.display) // casting string to number
            switch(val) {
                case "+":
                case "-":
                case "/":
                case "X":
                    this.calculator.operator = val
                    this.calculator.isNextInput = true
                    this.calculator.keys.push(num)
                    break;
                case ".":
                    this.calculator.display += val
                    break;
                case "=":
                    this.calculator.keys.push(num) 
                    this.executeTask()
                    break;
                case "DEL":
                    this.calculator.display = 0;
                    this.calculator.isNextInput = false
                    this.calculator.keys = []
                    break;
                
            }
        },
        executeTask() {
            let result = null
            let nums = this.calculator.keys
            if (this.calculator.keys.length < 2) return
            switch(this.calculator.operator) {
                case "+":
                    result = nums[0] + nums[1]
                    break;
                case "-":
                    result = nums[0] - nums[1]
                    break;
                case "X":
                    result = nums[0] * nums[1]
                    break;
                case "/":
                    if (nums[1] !== 0) {
                        result = nums[0] / nums[1]
                    }
                    else {
                        result = "Error - Cannot divide by zero"
                    }
                    break;
            }
            this.calculator.display = result
            this.calculator.keys = []
        },
        isNumber(str) {
            let reg = /^\d+$/;
            return reg.test(str)
        }
    }
}
</script>

<style scoped>
.calculator {
    width: 30vw;
    height: 50vh;
    margin: 0 auto;
    border: 1px solid rgb(54, 54, 54);
    background: wheat;
}
.grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    max-width: 400px;
    max-height: 600px;
    min-width: 300px;
}
.btn {
    width: 70%;
    height: 70%;
    outline: none;
    background: transparent;
    font-size: 18px;
    font-weight: 600;
    border: 1px solid rgb(54, 54, 54);
    padding: 10px;
    margin: 10px;
    border-radius: 25%;
    box-shadow: 1px 0 5px 0;
}
.btn:hover {
    cursor: pointer;
}
.calc-display {
    grid-area: 1 / 2 / span 1 / span 3;
    font-size: 18px;
    padding: 10px;
    background: white;
    opacity: 0.8;
}
</style>